body { margin: 0; padding-bottom: 3rem; font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; }

      #form { background: rgba(0, 0, 0, 0.15); padding: 0.25rem; position: fixed; bottom: 0; left: 0; right: 0; display: flex; height: 3rem; box-sizing: border-box; backdrop-filter: blur(10px); }
      #input { border: none; padding: 0 1rem; flex-grow: 1; border-radius: 2rem; margin: 0.25rem; min-width: 35%;}
      #input:focus { outline: none; }
      #form > button { background: #333; border: none; padding: 0 0.5rem; margin: 0.15rem; border-radius: 3px; outline: none; color: #fff; }

      #messages { list-style-type: none; padding: 0; display: flex; flex-direction: column;}
      #messages > li { padding: 0.5rem 1rem; }
      .sent-message { background: aqua; text-align: right;margin-bottom: 5px; margin-left: auto; margin-right: 10px; border-radius: 20px; max-width: 80%; word-wrap: break-word;}
      .received-message { background:rgb(87 222 32); margin-bottom: 5px; margin-right: auto; margin-left: 10px; border-radius: 20px; max-width: 80%; word-wrap: break-word;}
   
     